				location: '活动地点',
				start: '2019年xx月xx日',
				end: '',
				sponsorid: 100,
				sponsorname: 'xx学生会',
				type: 1,
				state: 200,
				reserved: false,
				like: false
			},
			current: 0,
			tabs: [
				"介绍", "动态"
			],
			sponsor: {
				avatarUrl: '',
				name: 'xx学生会',
				id: 0,
			},
			messagelist: [{
				"id": 0,
				"text": '1231241524',
				"appreciate": false
			},{
				"id": 1,
				"text": '1231241524124125',
				"appreciate": false
			}]
		};
	},
	onLoad(option) {
		console.log(option)
		// uni.request({
		// 	url: 'http://154.8.167.168:8080', //仅为示例，并非真实接口地址。
		// 	data: {
		// 		id: option.id
		// 	},
		// 	header: {
